body {
  margin: 0;
  padding: 0;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  max-width: 1130px;
  margin: auto;
}

.container-fluid {
  width: 100%;
}

.container,
.container-fluid {
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
}

.row {
  margin-left: -15px;
  margin-right: -15px;
  display: flex;
  flex-wrap: wrap;
}

.row:after {
  content: '';
  display: table;
  clear: both;
}

[class*="col-"] {
  padding-left: 15px;
  padding-right: 15px;
}

